.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #C2C2C2;
  text-transform: uppercase;
  font-weight: 600;
  overflow: auto;

  &-top {
    margin-bottom: 24px;
    position: sticky;
    top:0;
    z-index: 1;

    @include onTablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    font-size: 16px;
    line-height: 27px;
    color: $main-bg-color;
    text-decoration: none;
    margin-bottom: 16px;
  }

  &__book-a-test-ride{
    display: block;
    width: fit-content;

    color: $main-bg-color;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-bg-color;
  }
}
